<template>
  <div class="row" v-if="isReady">
    <div class="col-12 mt-3 flex-x-between">
      <div class="flex-align-center">
        <h3 class="my-0">Orders</h3>
      </div>

      <div class="flex-align-center flex-end">
        <el-button
          v-if="store.type === 'supplier'"
          class="mr-3 my-0"
          @click="$router.push('/update-fulfillment')"
          type="primary"
          v-ripple.400
        >
          Update Fulfillment
        </el-button>
        <el-button
          class="mx-0 my-0"
          @click="$router.push('/add-new-order')"
          type="primary"
          v-ripple.400
        >
          Add order
        </el-button>
      </div>
    </div>
    <div class="col-12 list-table mt-lg-3 mt-md-3 p-0 pt-sm-0 mt-sm-3">
      <div class="box mb-sm-3">
        <div class="row">
          <!-- <div class="col-12 flex-align-center">
            <p
              class="mx-0 table-tab"
              :class="filters.status === '' && 'active-table-tab'"
              @click="(filters.status = ''), (filters.page = 1)"
            >
              All
            </p>
            <p
              class="mx-0 table-tab"
              :class="filters.status === 'Active' && 'active-table-tab'"
              @click="(filters.status = 'Active'), (filters.page = 1)"
            >
              Active
            </p>
            <p
              :class="filters.status === 'Completed' && 'active-table-tab'"
              class="mx-0 table-tab"
              @click="(filters.status = 'Completed'), (filters.page = 1)"
            >
              Completed
            </p>
            <p
              :class="filters.status === 'Canceled' && 'active-table-tab'"
              class="mx-0 table-tab"
              @click="(filters.status = 'Canceled'), (filters.page = 1)"
            >
              Canceled
            </p>
            <p
              :class="filters.status == 'Unfulfilled' && 'active-table-tab'"
              class="mx-0 table-tab"
              @click="(filters.status = 'Unfulfilled'), (filters.page = 1)"
            >
              Unfulfilled
            </p>
            <p
              :class="filters.status === 'Unpaid' && 'active-table-tab'"
              class="mx-0 table-tab"
              @click="(filters.status = 'Unpaid'), (filters.page = 1)"
            >
              Unpaid
            </p>
            <p
              :class="filters.status === 'Archived' && 'active-table-tab'"
              class="mx-0 table-tab"
              @click="(filters.status = 'Archived'), (filters.page = 1)"
            >
              Archived
            </p>
          </div>
          <div class="col-12"><div class="b-divider mb-1 mt-0"></div></div> -->
          <div
            class="col-12 mb-1 mb-sm-0 d-flex justify-content-between align-items-center"
          >
            <div class="flex-align-center sm-hide">
              <!-- <el-select
                v-model="filters.pageSize"
                class="mr-2 page-size-selector mr-2"
              >
                <el-option
                  v-for="page in 10"
                  :key="page"
                  :label="page * 5"
                  :value="page * 5"
                >
                </el-option>
              </el-select> -->
              <el-input
                v-model="inputHelper"
                @change="setFilterQuery"
                class="mr-2 table-search"
                placeholder="Type something"
                prefix-icon="bx bx-search"
              >
              </el-input>

              <el-date-picker
                class="mr-2 filter-input"
                v-model="filters.date"
                type="date"
                format="dd/MM/yyyy"
                value-format="dd/MM/yyyy"
                placeholder="Pick a date"
              >
              </el-date-picker>
            </div>

            <el-popover placement="bottom" width="200" trigger="click">
              <p class="font-weight-bold">Sort By</p>
              <div class="b-divider my-2"></div>
              <div class="w-100">
                <el-radio
                  v-model="filters.order_by"
                  v-for="(sort, n) in sortList"
                  :key="sort.name"
                  :class="n !== sortList.length - 1 && 'mb-3'"
                  :label="sort.value"
                >
                  {{ sort.name }}
                </el-radio>
              </div>
              <el-button
                v-ripple.400="'rgba(0,0,0,0.1)'"
                class="flat ml-2 my-0 sm-hide no-width"
                slot="reference"
              >
                <i class="bx bx-sort mr-1"></i> Sort
              </el-button>
            </el-popover>

            <div class="md-hide w-100 lg-hide">
              <el-input
                v-model="inputHelper"
                @change="setFilterQuery"
                class="w-100 mb-sm-2"
                placeholder="Type something"
                prefix-icon="bx bx-search"
              >
              </el-input>

              <div class="flex-x-between">
                <el-date-picker
                  class="w-100"
                  v-model="filters.date"
                  type="date"
                  format="dd/MM/yyyy"
                  value-format="dd/MM/yyyy"
                  placeholder="Pick a date"
                >
                </el-date-picker>

                <el-popover placement="bottom" width="200" trigger="click">
                  <p class="font-weight-bold">Sort By</p>
                  <div class="b-divider my-2"></div>
                  <div class="w-100">
                    <el-radio
                      v-model="filters.order_by"
                      v-for="(sort, n) in sortList"
                      :key="sort.name"
                      :class="n !== sortList.length - 1 && 'mb-3'"
                      :label="sort.value"
                    >
                      {{ sort.name }}
                    </el-radio>
                  </div>
                  <el-button
                    v-ripple.400="'rgba(0,0,0,0.1)'"
                    class="flat ml-2 my-0 no-width"
                    slot="reference"
                  >
                    <i class="bx bx-sort mr-1"></i> Sort
                  </el-button>
                </el-popover>
              </div>
            </div>
          </div>
        </div>
        <div class="w-100 sm-hide el-main-table">
          <!-- <div class="el-table-header" v-if="selected.length > 0">
            <div class="d-flex align-items-center">
              <el-checkbox
                :indeterminate="selected.length == sales.results.length"
                v-model="allCheck"
                @change="$refs.multipleTable.toggleAllSelection"
                >{{ selected.length }} selected</el-checkbox
              >
              <el-dropdown @command="updateStatus" trigger="click">
                <p
                  class="el-dropdown-link ml-3 pointer primary hover-underline"
                >
                  Actions
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </p>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    v-for="action in actions"
                    :key="action.value"
                    :command="action.value"
                  >
                    {{ action.label }}</el-dropdown-item
                  >
                </el-dropdown-menu>
              </el-dropdown>
            </div>
          </div> -->

          <el-table
            ref="multipleTable"
            class=""
            :data="sales.results"
            style="width: 100%"
            @row-click="
              (row) =>
                $router.push({
                  name: 'Edit order',
                  params: { id: row.id },
                })
            "
          >
            <!-- <el-table-column type="selection"> </el-table-column> -->
            <el-table-column label="Order" property="sale_no" width="100">
            </el-table-column>
            <el-table-column width="150" label="Date of order">
              <template slot-scope="scope">
                {{ scope.row.date }}
              </template>
            </el-table-column>

            <el-table-column
              label="Plan"
              v-if="store.type == 'supplier'"
              width="150"
            >
              <template slot-scope="scope">
                {{
                  (scope.row.delivery_company_name == "safe delivery"
                    ? "Normal Plan"
                    : scope.row.delivery_company_name) || "-"
                }}
              </template>
            </el-table-column>

            <el-table-column
              v-if="store.type !== 'supplier'"
              label="Type"
              width="150"
            >
              <template slot-scope="scope">
                <el-tag
                  :type="scope.row.type == 'In Stock' ? 'success' : 'warning'"
                  class="rounded"
                >
                  {{
                    scope.row.type == "Instock" ? "-" : scope.row.type
                  }}</el-tag
                >
              </template>
            </el-table-column>

            <el-table-column label="Note" width="150">
              <template slot-scope="scope">
                {{ scope.row.note || "-" }}
              </template>
            </el-table-column>

            <el-table-column label="Exported By" width="150">
              <template slot-scope="scope">
                {{ scope.row.exported_by || "-" }}
              </template>
            </el-table-column>

            <el-table-column label="Customer" width="150">
              <template slot-scope="scope">
                {{ scope.row.customer_name || "-" }}
              </template>
            </el-table-column>
            <el-table-column label="Total" width="150">
              <template slot-scope="scope">
                {{
                  scope.row.total + (store.type === "supplier" ? "¥" : "MMK")
                }}
              </template>
            </el-table-column>

            <el-table-column
              v-if="store.type == 'supplier'"
              label="Type"
              width="150"
            >
              <template slot-scope="scope">
                {{ scope.row.profit_amount + "¥" }}
              </template>
            </el-table-column>

            <el-table-column label="Payment" width="150">
              <template slot-scope="scope">
                <el-tag
                  type="success"
                  class="rounded"
                  v-if="scope.row.payment_status == 'Paid'"
                  ><i class="bx bx-check-circle"></i>
                  {{ scope.row.payment_status }}</el-tag
                >

                <el-tag
                  type="danger"
                  class="rounded"
                  v-if="scope.row.payment_status == 'Unpaid'"
                  ><i class="bx bx-time-five"></i
                  >{{ scope.row.payment_status }}</el-tag
                >

                <el-tag
                  type="warning"
                  class="rounded"
                  v-if="scope.row.payment_status == 'Partially Paid'"
                  ><i class="bx bx-time-five"></i
                  >{{ scope.row.payment_status }}</el-tag
                >
              </template>
            </el-table-column>
            <el-table-column
              label="Code"
              v-if="store.type == 'supplier'"
              width="150"
            >
              <template slot-scope="scope">
                <el-tag
                  type="success"
                  v-if="isCompleteCode(scope.row.products) == 'Complete'"
                  class="rounded"
                >
                  <i class="bx bx-check-circle"></i> Completed</el-tag
                >
                <el-tag
                  type="danger"
                  v-if="isCompleteCode(scope.row.products) == 'No code'"
                  class="rounded"
                >
                  <i class="bx bx-time-five"></i> Missing</el-tag
                >

                <el-tag
                  type="warning"
                  v-if="isCompleteCode(scope.row.products) == 'Has some codes'"
                  class="rounded"
                >
                  <i class="bx bx-time-five"></i> Has some codes</el-tag
                >
              </template>
            </el-table-column>

            <el-table-column label="Fulfilled">
              <template slot-scope="scope">
                <el-tag
                  type="success"
                  v-if="isFulfilled(scope.row)"
                  class="rounded"
                >
                  <i class="bx bx-check-circle"></i> Fulfilled</el-tag
                >
                <el-tag type="danger" v-else class="rounded">
                  <i class="bx bx-time-five"></i> Unfulfilled</el-tag
                >
              </template>
            </el-table-column>
            <!-- <el-table-column label="Items">
              <template slot-scope="scope">
                {{ getItemsQuantity(scope.row) }} Items
              </template>
            </el-table-column> -->

            <template #empty>
              <el-empty description="No data found!">
                <template #image> <empty /> </template
              ></el-empty>
            </template>
          </el-table>
          <div class="flex-center sm-hide" v-loading="loading">
            <p class="py-3"></p>
          </div>
        </div>
      </div>

      <div
        v-for="sale in sales.results"
        :key="sale.id"
        class="sale-item box lg-hide md-hide mb-sm-3"
        @click="
          () =>
            $router.push({
              name: 'Edit order',
              params: { id: sale.id },
            })
        "
      >
        <div class="flex-x-between mb-2">
          <p class="sm-text">{{ sale.sale_no }}</p>
          <p class="sm-text">{{ sale.date }}</p>
        </div>

        <div class="flex-x-between mb-2">
          <p class="text-bold">{{ sale.customer_name }}</p>
          <p class="primary text-bold">
            {{ sale.total + (store.type === "supplier" ? "¥" : "MMK") }}
          </p>
        </div>

        <div class="flex-align-center flex-wrap">
          <el-tag type="info" class="rounded mr-2 mb-2"
            ><i class="bx bx-user"></i> {{ sale.exported_by }}</el-tag
          >

          <el-tag
            :type="sale.type == 'In Stock' ? 'success' : 'warning'"
            class="rounded mr-2 mb-2"
          >
            {{ sale.type == "Instock" ? "-" : sale.type }}</el-tag
          >

          <el-tag
            type="success"
            class="rounded mr-2 mb-2"
            v-if="sale.payment_status == 'Paid'"
            ><i class="bx bx-check-circle"></i>
            {{ sale.payment_status }}</el-tag
          >

          <el-tag
            type="danger"
            class="rounded mr-2 mb-2"
            v-if="sale.payment_status == 'Unpaid'"
            ><i class="bx bx-time-five"></i>{{ sale.payment_status }}</el-tag
          >

          <el-tag
            type="warning"
            class="rounded mr-2 mb-2"
            v-if="sale.payment_status == 'Partially Paid'"
            ><i class="bx bx-time-five"></i>{{ sale.payment_status }}</el-tag
          >

          <el-tag
            type="success"
            v-if="
              isCompleteCode(sale.products) == 'Complete' &&
              store.type == 'supplier'
            "
            class="rounded mr-2 mb-2"
          >
            <i class="bx bx-check-circle"></i>Code Completed
          </el-tag>
          <el-tag
            type="danger"
            v-if="
              isCompleteCode(sale.products) !== 'Complete' &&
              store.type == 'supplier'
            "
            class="rounded mr-2 mb-2"
          >
            <i class="bx bx-time-five"></i>Code Missing</el-tag
          >

          <el-tag
            type="success"
            v-if="isFulfilled(sale)"
            class="rounded mr-2 mb-2"
          >
            <i class="bx bx-check-circle"></i> Fulfilled</el-tag
          >
          <el-tag type="danger" v-else class="rounded mr-2 mb-2">
            <i class="bx bx-time-five"></i> Unfulfilled</el-tag
          >
        </div>
        <p class="sm-text mt-2">Note : {{ sale.note }}</p>
      </div>

      <div class="lg-hide box md-hide mb-3" v-loading="loading">
        <p class="py-3"></p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { empty } from "../../common";
export default {
  data() {
    return {
      inputHelper: "",
      filters: {
        query: "",
        order_by: "-created_at",
        pageSize: 25,
        status: "",
        delivery: "",
        date: "",
        is_fulfilled: false,
        payment_status: "",
        page: 1,
      },
      loading: false,
      actions: [
        {
          label: "Archive sales",
          value: "Archived",
        },
        {
          label: "Set as Active",
          value: "Active",
        },
        {
          label: "Set as Completed",
          value: "Completed",
        },
        {
          label: "Set as Canceled",
          value: "Canceled",
        },
      ],
      sortList: [
        {
          name: "Created (oldest first)",
          value: "created_at",
        },
        {
          name: "Created (newest first)",
          value: "-created_at",
        },
        {
          name: "Updated (oldest first)",
          value: "updated_at",
        },
        {
          name: "Updated (newest first)",
          value: "-updated_at",
        },
        {
          name: "Customer A–Z",
          value: "customer__name",
        },
        {
          name: "Customer Z–A",
          value: "-customer__name",
        },
      ],
      loadingDelivey: false,
      deliveries: [],
      selectedDate: null,
      selectedDelivery: null,
      allCheck: false,
      gettingOrder: false,
      page: 1,
      max: 5,
      selected: [],
      isReadyToGet: false,
      sales: {
        current_page: 1,
        results: [],
        total_pages: 0,
        next: 1,
      },
      scroll: 0,
    };
  },
  components: { empty },
  computed: {
    ...mapState(["isReady", "store"]),
  },
  created() {
    this.$store.commit("setIsReady", false);
    this.getDeliveries();
  },
  watch: {
    filters: {
      handler: function (value) {
        if (this.isReadyToGet) {
          this.$router
            .push({
              path: "/orders",
              query: {
                status: value.status,
                query: value.query,
                page_size: value.pageSize,
                date: value.date,
                page: value.page,
                order_by: value.order_by,
              },
            })
            .catch(() => {});
        }
      },
      deep: true,
    },
  },
  mounted() {
    this.$store.commit("setSidebar", false);
    this.filters = {
      query: this.$route.query.query || "",
      order_by: this.$route.query.order_by || "-created_at",
      pageSize: this.$route.query.page_size || "25",
      status: this.$route.query.status || "",
      delivery: "",
      date: this.$route.query.date || "",
      page: this.$route.query.page || "1",
      is_fulfilled: false,
      payment_status: "",
    };
    (this.inputHelper = this.$route.query.query || ""),
      (this.isReadyToGet = true);
    this.getSales();
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },

  methods: {
    handleScroll(e) {
      const scrollHeight =
        e.target.scrollingElement.scrollHeight -
        e.target.scrollingElement.clientHeight * 3;
      if (scrollHeight < e.target.scrollingElement.scrollTop) {
        if (!this.loading) {
          this.getSales();
        }
      }
    },
    setFilterQuery() {
      this.filters.query = this.inputHelper;
    },
    isFulfilled(order) {
      var val = true;
      order.products.forEach((product) => {
        if (
          parseFloat(product.quantity) !==
          parseFloat(product.number_of_fullfilled)
        ) {
          val = false;
        }
      });
      return val;
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.selected = val;
    },
    changeActions(action) {
      if (action == "Archive sales") {
        this.updateStatus("Archived");
      } else if (action == "Unarchive sales") {
        this.updateStatus("Active");
      }
    },
    updateStatus(status) {
      this.updatingStatus = true;
      var selectedIds = [];
      this.selected.forEach((sale) => selectedIds.push(sale.id));
      this.$axios
        .put("sales-status/", {
          status: status,
          ids: selectedIds,
        })
        .then(() => {
          this.selected = [];
          this.getSales();
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    getSales() {
      if (this.sales.next) {
        this.loading = true;
        this.gettingOrder = true;
        if (this.filters.date === null) {
          this.filters.date = "";
        }

        var url = `sales/?page=${this.sales.next}&page_size=${this.filters.pageSize}&location=&query=${this.filters.query}&status=${this.filters.status}&delivery=${this.filters.delivery}&date=${this.filters.date}&order_by=${this.filters.order_by}`;
        this.$axios
          .get(url)
          .then((response) => {
            const scroll = window.scrollY;
            this.sales = {
              ...response.data,
              results: [...this.sales.results, ...response.data.results],
            };
            setTimeout(() => {
              window.scroll(0, scroll);
              this.loading = false;
            }, 800);

            this.$store.commit("setIsReady", true);
          })
          .catch((err) => {
            this.loading = false;
            this.gettingOrder = false;
            this.$noti.display({
              message: err,
              status: "error",
              color: "error",
            });
          });
      }
    },
    getDeliveries() {
      var url = `deliveries/?page=1&query=&order_by=-name&page_size=1000000`;
      this.$axios
        .get(url)
        .then((response) => {
          this.loadingDelivery = false;
          this.deliveries = response.data.results;
        })
        .catch((err) => {
          this.loadingDelivery = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    isCompleteCode(items) {
      var val = "Complete";
      var hasSomeCode = false;

      items.forEach((item) => {
        if (!item.link) {
          val = "No code";
        } else {
          hasSomeCode = true;
        }
      });
      if (val == "No code" && hasSomeCode) {
        return "Has some codes";
      }
      return val;
    },
    getItemsQuantity(sale) {
      return sale.products.reduce((total, item) => {
        return total + parseFloat(item.quantity);
      }, 0);
    },
  },
};
</script>

<style >
</style>